@keyframes fade {
  from {
    visibility: hidden;
    opacity: 0;
    transform: translateX(0px);
  }
  to {
    visibility: visible;
    opacity: 1;

    transform: translateX(16.5px);
  }
}

@keyframes fadeDown {
  from {
    visibility: hidden;
    opacity: 0;
    transform: translateY(0px);
  }
  to {
    visibility: visible;
    opacity: 1;
    transform: translateY(16.5px);
  }
}

